.page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
    width: 100%;
    flex-grow: 1;
    background-color: #f7f7f7;
    padding: 30px;
    box-sizing: border-box;
}

.page .card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-width: 40%;
    min-height: 60%;
    /* padding: 0 90px; */
    border-radius: 25px;
    box-shadow: 5px 5px 20px;
    background-color: white;
    box-sizing: border-box;
    overflow: hidden;
}

.page .content {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    align-items: center;
    justify-content: center;
    padding: 50px 90px;
    gap: 5px;
}

.page .selectSection {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
}

.page .selectSection button {
    flex-grow: 1;
    background-color: white;
    min-height: fit-content;
    height: 45px;
    color: #1750a8;
    /* border: 1px 0px 1px 0px; */
    border: none;
    /* border-left: 1px solid black; */
    padding-left: 15px;
    padding-right: 15px;
    background-color: #e0ecfd;
}

.selectSection button:hover {
    background-color: #f6fafe;
}

.selectSection button.selected {
    background-color: white;
    border: none;
}

.page form {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;
}

.page form .inputs {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    align-items: flex-start;
}